<template>
  <div class="container">
    <page-title first-title="商家列表"></page-title>
    <div class="search-wrapper">
      <el-form :inline="true" :model="searchParams" class="demo-form-inline">
        <el-form-item label="店铺名称">
          <el-input v-model="searchParams.shop_name"></el-input>
        </el-form-item>
        <el-form-item label="企业名称">
          <el-input v-model="searchParams.suppliers_name"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="list" style="width: 100%" v-loading="isLoading" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column prop="suppliers_id" label="店铺ID"></el-table-column>
        <el-table-column prop="shop_name" label="店铺名称"></el-table-column>
        <el-table-column prop="suppliers_name" label="企业名称"></el-table-column>
        <el-table-column prop="manager_name" label="联系人"></el-table-column>
        <el-table-column prop="cooperate_role" label="商家类型"></el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="viewDetail(scope.row.suppliers_id)" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="searchParams.size"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import { getSuppliersList } from '../../api/suppliers'
import { mapMutations } from 'vuex'

export default {
  name: 'SuppliersList',
  data() {
    return {
      total: 0,
      currentPage: 1,
      searchParams: { page: 1, size: 10, shop_name: '', suppliers_name: '' },
      list: [],
      isLoading: false //是否正在加载
    }
  },
  created() {
    this.getList()
  },
  methods: {
    ...mapMutations({
      setSuppliersId: 'setSuppliersId'
    }),
    getList() {
      this.isLoading = true
      getSuppliersList(this.searchParams)
        .then(
          res => {
            this.list = res.list
            this.total = res.paged.total
          },
          error => {
            console.log(error)
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    },
    clear() {
      this.searchParams.page = 1
      this.searchParams.size = 10
      this.searchParams.shop_name = ''
      this.searchParams.suppliers_name = ''
    },
    viewDetail(id) {
      this.setSuppliersId(id)
      this.$router.push({ name: 'SuppliersAccount', params: { id: id } })
    },
    handleCurrentChange(currentPage) {
      this.searchParams.page = currentPage
      this.getList()
    },
    search() {
      this.searchParams.page = 1
      this.getList()
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}

.container {
  font-size: 15px;
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 0;
    }
  }
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
